import React from 'react'
import Footer from '../components/genericComponents/Footer'
import Nav from '../components/genericComponents/Nav'
import '../components/genericComponents/styles/home.css'
import {getCases} from '../services/CasesServices'
import {getCase} from '../services/CasesServices'
import CaseList from '../components/caseList'


class Home extends React.Component {
    constructor (props) {
        super(props)
        this.state = {
          cases: [],
          caseId: ''
        }
        this.loadList = this.loadList.bind(this)
        this.getUsername = this.getUsername.bind(this)
        this.onSetDonation = this.onSetDonation.bind(this)
      }
      getUsername(){
    
        return localStorage.getItem("user-data")
    }

    onSetDonation(event){
      event.preventDefault()
      const id_case = event.target.id
      console.log("id: " + id_case)
      getCase({id_case: id_case})
      .then((record) =>{
        this.props.history.push({
          pathname: "/makeDonation",
          search: `?search=${record._id}&titulo=${record.case_title}&desc=${record.case_description}&ciudad=${record.case_city}&estado=${record.case_state}`
          }) 
      })
    }

      loadList () {
        return getCases()
          .then(cases => {
            this.setState({
              cases: cases
            })
          })
      }
      componentDidMount () {
        this.loadList()
      }

    render(){
        return(
            <div>
            <Nav></Nav>
            <div className="container">
              <div>
                <h3>Bienvenid@ {this.getUsername()}</h3>
              </div>
              <br></br>
              <CaseList cases={this.state.cases} onSetDonation={this.onSetDonation}/>
            </div>
            
            <Footer></Footer>
        </div>
        )
    }
        
    
}
export default Home