import React from 'react'
import {withRouter} from 'react-router-dom'
import {registerUser} from '../services/UserServices'
import Nav from  '../components/genericComponents/Nav'
import Footer from '../components/genericComponents/Footer'
import '../components/genericComponents/styles/register.css'
import {Link} from 'react-router-dom'


class Register extends React.Component {

    constructor(props) {
        super(props)
            this.state = {
                user_email: '',
                user_password: '',
                user_name : '',
                user_phone : '',
                user_city : '',
                user_state : '',
                user_zipcode : '',
                user_message: '',
                user_flag : false
            }
            this.onChangeEmail = this.onChangeEmail.bind(this)
            this.onChangePassword = this.onChangePassword.bind(this)
            this.onChangeCity = this.onChangeCity.bind(this)
            this.onChangeZipCode = this.onChangeZipCode.bind(this)
            this.onChangeUserName = this.onChangeUserName.bind(this)
            this.onChangePhone = this.onChangePhone.bind(this)
            this.getEstado = this.getEstado.bind(this)
            this.onRegisterUser = this.onRegisterUser.bind(this)
            this.verifyUserCreated = this.verifyUserCreated.bind(this)
        
    }
    
    onRegisterUser (event) {
        
        event.preventDefault()
        //const { case_title,case_description,case_city,case_state,case_zipcode,case_initial_budget } = this.state
        const user = { 
        user_email: this.state.user_email,
        user_password:this.state.user_password,
        user_name: this.state.user_name,
        user_phone: this.state.user_phone,
        user_city:this.state.user_city,
        user_state:this.state.user_state,
        user_zipcode:this.state.user_zipcode,
        }
        
        registerUser(user)
          .then(() => {
            this.setState({
                user_email: '',
                user_password: '',
                user_name : '',
                user_phone : '',
                user_city :'',
                user_state : '',
                user_zipcode : '',
                user_message : "Usuario Creado",
                user_flag: true
            })
          })
      }

      

    onChangeEmail(e){
        e.preventDefault()
        this.setState({user_email:e.target.value,user_message:''
        })
        
    }
    onChangePassword(e){
        e.preventDefault()
        this.setState({user_password:e.target.value,user_message:''})
       
    }
    onChangeUserName(e){
        e.preventDefault()
        this.setState({user_name:e.target.value,user_message:''
        })
        
    }
    onChangeCity(e){
        e.preventDefault()
        this.setState({user_city:e.target.value,user_message:''})
       
    }
    onChangePhone(e){
        e.preventDefault()
        this.setState({user_phone:e.target.value,user_message:''})
    }

    onChangeZipCode(e){
        e.preventDefault()
        this.setState({user_zipcode:e.target.value,user_message:''})
    }
   
    getEstado(e) {
        e.preventDefault()
        this.setState({user_state:e.target.value})
    }

    verifyUserCreated(){
        if(this.state.user_flag === true){
            return(
            <div className="form-group col-md-15">
            <button type="button" className="btn btn-outline-primary" onClick={this.onRegisterUser}>Registrar Usuario</button>
                <small id="case_description_help" className="form-text text-muted">
                                    {this.state.user_message}
                </small>
                <Link className="btn btn-outline-primary" to="/login">Log In</Link>
            </div>
            )
        }else{
            return(
                <div className="form-group col-md-15">
                <button type="button" className="btn btn-outline-primary" onClick={this.onRegisterUser}>Registrar Usuario</button>
                    <small id="case_description_help" className="form-text text-muted">
                                        {this.state.user_message}
                    </small>
                </div>
               
                )
        }
    }
    render(){

    return(
        <div>
            <Nav />
            <div class="container" >
            <form>
                <h3>Registro de Usuario</h3>
                <br></br>
                    <div className="row">
                            <div className="form-group col-md-6">
                            <input type="text" className="form-control col-md-7" placeholder="Email" value={this.state.user_email} onChange={this.onChangeEmail}/>
                                <small id="case_description_help" className="form-text text-muted">
                                    Escribe tu correo electronico
                                </small>
                            </div>
                            <div className="form-group col-md-6">
                                <input type="password" className="form-control col-md-7" placeholder="Password" value={this.state.user_password} onChange={this.onChangePassword}/>
                                <small id="case_description_help" className="form-text text-muted">
                                    Tu Password debe ser de 8 digitos minimo
                                </small>
                            </div>
                            <div className="form-group col-md-6"></div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-6">
                            <input type="text" className="form-control col-md-12" placeholder="Nombre y Apellidos"  value={this.state.user_name} onChange={this.onChangeUserName} />
                                <small id="case_description_help" className="form-text text-muted">
                                    Escribe tu nombre y apellidos
                                </small>
                            </div>
                            <div className="form-group col-md-6">
                            <input type="text" className="form-control col-md-5" placeholder="Telefono de contacto"  value={this.state.user_phone} onChange={this.onChangePhone} />
                                <small id="case_description_help" className="form-text text-muted">
                                    Escribe un telefono donde te podamos encontrar
                                </small>
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-6">
                                <input type="text" className="form-control col-md-8" placeholder="Ciudad" value= {this.state.user_city} onChange={this.onChangeCity} />
                            </div>
                            
                            <div className="form-group col-md-6">
                                <input type="text" className="form-control col-md-6" placeholder="Codigo Postal" value={this.state.user_zipcode} onChange={this.onChangeZipCode} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-6">
                                <select id="inputState" className="form-control" onChange={this.getEstado}>
                                    <option selected>Estado...</option>
                                    <option value="Ciudad de Mexico">Cd Mx</option>
                                    <option value="Jalisco">Jalisco</option>
                                    <option value="Queretaro">Queretaro</option>
                                    <option value="Otro">Otro</option>
                                </select>
                            </div>
                            {this.verifyUserCreated()}
                        </div>
                        
            </form>
            </div>
            <Footer></Footer>
        </div>
    )
    }
}
export default withRouter(Register)