//const BASE_URL = 'http://localhost:5000/'
const BASE_URL = 'https://helpet-mex.herokuapp.com/'
const token = localStorage.getItem('token')

export function getCases () {
    return fetch(`${BASE_URL}cases/getCases`, {
      method: 'GET', 
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(response => response.json())
      .then(data => {
          console.log("data: " + data)
        let case_map = data.map((todo) => {
          return todo
        })
        return case_map
      })
  }

  export function publishCase(case_row){
    console.log("entrando a publish")
    return fetch(`${BASE_URL}cases/publishCase`,{
    method: "POST",
    headers: {
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + token
    },
    body: JSON.stringify(case_row)
    })
    .then(response => {
      return response.json()})
    .catch(err => {
      console.log(err)
    })
  }

  export function getCase (id_case) {
    return fetch(`${BASE_URL}cases/getCase`, {
      method: 'POST', 
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(id_case)
    })
    .then(function(response) {
      return response.json();
    })
  }

  export function updateEstimateCase(case_donation) {
    return fetch(`${BASE_URL}cases/makeDonation`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + token
      },
      body: JSON.stringify(case_donation)
    })
      .then(response => {return response.json()})
      .catch(err => console.log(err))
  }