import React, {Component} from 'react'
import './styles/nav.css';
import Auth from '../../Helper/Auth'
import {Link} from 'react-router-dom'
import Jumbotron from './Jumbotron';
class Nav extends Component {
    logout(){

        return Auth.logout(() =>{
            console.log('Goodbye')
           
        })

    }
    verifyIfItsAuth() {
        if (Auth.isAuthenticated()) {
            return <div className="navbar-nav">
                <Link className="nav-item nav-link" to="/">Inicio</Link>
                <Link className="nav-item nav-link" to="/publishCase">Publicar Caso</Link>
                <Link className="btn btn-link" onClick={this.logout}> Cerrar Sesion</Link>
            </div>

        } else {
            return <div className="navbar-nav">

                <Link className="nav-item nav-link active" to="/">Inicio <span className="sr-only">(current)</span></Link>
                <Link className="nav-item nav-link" to='/login' >Iniciar Sesion</Link>
                <Link className="btn btn-primary" to='/register' >Registrarse</Link>

            </div>
        }
    }

    

   
    render() {
        return (
            <div>
            <Jumbotron title="Helpet Mexico"></Jumbotron>
            <div className="wrap-nav">
                <nav className="navbar fixed-top navbar-toggleable-sm">
                    <div className="navbar-cont" id="navbarNavAltMarkup">
                        <div className="navbar-nav">
                        {this.verifyIfItsAuth()}
                        </div>
                    </div>
                </nav>
            </div>
          
          </div>
        )
    }
}
export default Nav