import React from 'react'
import {withRouter} from 'react-router-dom'
import Auth from '../Helper/Auth'
import {getToken} from '../services/UserServices'
import Nav from  '../components/genericComponents/Nav'
import Footer from '../components/genericComponents/Footer'



class Login extends React.Component {

    constructor(props) {
        super(props)
            this.state = {
                email: '',
                password: '',
                error: false,
                errorMessage: ''
            }
            this.onChangeEmail = this.onChangeEmail.bind(this)
            this.onChangePassword = this.onChangePassword.bind(this)
            this.authUser = this.authUser.bind(this)
    }
    authUser = (e) => {
        e.preventDefault()
        const {email, password} = this.state
        getToken({
        user_email: email,
        user_password: password,
        }).then(data => {
            
            if(data.success === false) {
                this.setState({
                    error: true,
                    errorMessage: data.message
                })
            } else {
                this.setState({
                    error:false,
                    errorMessage: ''
                })
                Auth.login(data, () => {
                    this.props.history.push("/")
                })
            }
        })

    }

    onChangeEmail(event){
        event.preventDefault()
        this.setState({
          email: event.target.value
        })
        
    }
    onChangePassword(event){
        event.preventDefault()
        this.setState({
          password: event.target.value
        })
    }
    render(){

    return(
        <div>
            <Nav />
            <div className="container">
            <h3>Bienvenido a Helpet Mexico</h3>
                <br></br>
                <form>
                <div className="form-group col-md-6">
                    <label for="exampleInputEmail1">Email address</label>
                    <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" onChange={this.onChangeEmail}  />
                    <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                </div>
                <div class="form-group col-md-6">
                    <label for="exampleInputPassword1">Password</label>
                    <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" onChange={this.onChangePassword} />
                </div>
                <div class="form-group col-md-6">
                    <button className="btn btn-primary" onClick={this.authUser}>Submit</button>
                    <small id="emailHelp" className="form-text text-muted">{this.state.errorMessage}</small>
                </div>
                </form>
            </div>
            <Footer></Footer>
        </div>
    )
    }
}
export default withRouter(Login)