import React from 'react'
import {withRouter} from 'react-router-dom'
import Nav from  '../components/genericComponents/Nav'
import Footer from '../components/genericComponents/Footer'
import {updateEstimateCase} from '../services/CasesServices'
import queryString from 'query-string'


class CaseDonation extends React.Component {

    constructor(props) {
        super(props)
            this.state = {
                donation_amount: '',
                donation_comments: '',
                donation_email: '',
                case_title: ''
            }
            this.onChangeAmount = this.onChangeAmount.bind(this)
            this.onChangeComments = this.onChangeComments.bind(this)
            this.onMakeDonation = this.onMakeDonation.bind(this)
    
    }
    
    onChangeAmount(event){
        event.preventDefault()
        this.setState({
          donation_amount: event.target.value
        })
        
    }
    onChangeComments(event){
        event.preventDefault()
        this.setState({
          donation_comments: event.target.value
        })
    }

    componentDidMount () {
        let caso_donar = queryString.parse(this.props.location.search)
        
            let case_id =caso_donar.search
            let case_title = caso_donar.titulo
            let case_city =caso_donar.ciudad
            let case_state = caso_donar.estado
            let case_desc = caso_donar.desc
        console.log(caso_donar.estado)
        
         this.setState({

            case_title:case_title,
            case_id: case_id,
            case_city: case_city,
            case_state:case_state,
            case_description:case_desc

        })
      }

      onMakeDonation(event){
        event.preventDefault()
        const case_donation = {
           case_id: event.target.id,
           donation_amount: this.state.donation_amount,
           donation_comments: this.state.donation_comments,
        }
        
        console.log("donacion: " + case_donation.donation_amount)
        updateEstimateCase(case_donation)
        .then((record) =>{
          this.props.history.push("/")
        })
      }


    render(){

    return(
        <div>
            <Nav />
            <div className="container">
            <h3>Caso: {this.state.case_title}</h3>
                <br></br>
                <form>
                <div className="form-group col-md-6">
                    <div id="caso">
                    <label for="descripcion">Descripcion:   </label>
                    <p> {this.state.case_description} reportado en la ciudad de {this.state.case_city} en el estado de {this.state.case_state}
                    </p>
                    </div>
                </div>
                </form>
                <hr></hr>
                <form>
                    <div className="form-group col-md-6">
                        <label><b>Monto a Donar:</b> </label>
                        <input type="text" className="form-control col-md-7" placeholder="Monto a Donar" id="monto" value={this.state.donation_amount} onChange={this.onChangeAmount} />
                        <small id="case_description_help" className="form-text text-muted">Monto a Donar
                        </small>
                    </div>
                    <div className="form-group col-md-6">
                        <label for="descripcion"><b>Comentarios:</b> </label>
                        <textarea className="form-control col-md-7" placeholder="Comentarios" id="comentarios" value={this.state.donation_comments} onChange={this.onChangeComments} />
                        <small id="case_description_help" className="form-text text-muted">Comentarios para el rescatista/adoptante
                        </small>
                        <a href="/" className="btn btn-success"  id={this.state.case_id} onClick={this.onMakeDonation}>Hacer Donacion</a>
                    </div>
                
                </form>
            </div>
            <Footer></Footer>
        </div>
    )
    }
}
export default withRouter(CaseDonation)