import React from 'react'
function caseList(props) {

  const items = props.cases.map((case_row, index) => (
    <div className="carousel-item">
      <div className="card text-center">
          <div className="card-header">
            {case_row.case_title}
          </div>
          <div className="card-body">
                <img className="card-img" src="https://albertoosornio.github.io/helpet-app/fondoCaso3.jpeg" alt="helpet mexico"/>
                <div className="card-img-overlay">
                  <h5 className="card-title">Estimado Necesario: ${case_row.case_initial_budget}.00 &nbsp;MXN</h5>
                  <p className="card-text">{case_row.case_description}, se reporta desde  {case_row.case_city}  &nbsp; 
                  en el estado de {case_row.case_state}
                  </p>
                  <div className="btn-card">
                    <a href="/" className="btn btn-info disabled">Ver Detalles</a>&nbsp;&nbsp;
                    <a href="/" className="btn btn-success"  id={case_row._id} onClick={props.onSetDonation}>Hacer Donacion</a>
                  </div>
                </div>  
          </div>
      </div>
    </div>
    ))


  return (
    <div id="carouselExampleControls" className="carousel slide" data-ride="carousel">
      <div className="carousel-inner">
        <div className="carousel-item active">
          <div className="card text-center text-black">
            <div className="card-header">
              Helpet Mexico
            </div>
            <img className="card-img" src="https://albertoosornio.github.io/helpet-app/fondoCaso3.jpeg" alt="helpet mexico"/>
            <div className="card-img-overlay">
              <h5 className="card-title">Uno menos en la calle</h5>
              <p className="card-text"><br/>Cuando llegaste no sabia que hacer contigo, ahora no se que hacer sin ti <br/><br/><br/><br/></p>
             
              <p className="card-text">Total Donaciones: </p>
          </div>
        </div>
        </div>
          {items}
      </div>
      <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </a>
    </div>
  )
}


export default caseList