import React from 'react'
import {withRouter} from 'react-router-dom'
import Footer from '../components/genericComponents/Footer'
import Nav from '../components/genericComponents/Nav'
import '../components/genericComponents/styles/home.css'
import {publishCase} from '../services/CasesServices'

class PublishCase extends React.Component {
    
    constructor (props) {
        super(props)
        this.state = {
          case_owner: '',
          case_title: '',
          case_description: '',
          case_city: '',
          case_state: '',
          case_zipcode: '',
          case_initial_budget: '',
          message:''
        }
        this.onChangeTitle = this.onChangeTitle.bind(this)
        this.onChangeCity = this.onChangeCity.bind(this)
        this.onChangeZipCode = this.onChangeZipCode.bind(this)
        this.onChangeDesc = this.onChangeDesc.bind(this)
        this.onChangeBudget = this.onChangeBudget.bind(this)
        this.onChange = this.onChange.bind(this)
        this.getEstado = this.getEstado.bind(this)
        this.onPublishCase = this.onPublishCase.bind(this)
        
    }


    onPublishCase (event) {
        
        event.preventDefault()
        //const { case_title,case_description,case_city,case_state,case_zipcode,case_initial_budget } = this.state
        const case_row = { 
        case_owner: localStorage.getItem("user-data"),
        case_title:this.state.case_title,
        case_description: this.state.case_description,
        case_city:this.state.case_city,
        case_state:this.state.case_state,
        case_zipcode:this.state.case_zipcode,
        case_initial_budget:this.state.case_initial_budget
        }
        //console.log("estado actual: " + case_row.case_city)
        
        publishCase(case_row)
          .then(() => {
            this.setState({
                case_owner: "",
                case_title: "",
                case_description: "",
                case_city: "",
                case_state: "",
                case_zipcode: "",
                case_initial_budget: "0",
                message:"Caso publicado!!"
            })

          })
      }

      getUsername(){
        
        return localStorage.getItem("user-data")
    }

    onChangeTitle(e){
        e.preventDefault()
        this.setState({case_title:e.target.value,message:''
        })
        
    }
    onChangeDesc(e){
        e.preventDefault()
        this.setState({case_description:e.target.value,message:''})
       
    }
    onChangeCity(e){
        e.preventDefault()
        this.setState({case_city:e.target.value,message:''})
       
    }
    onChangeBudget(e){
        e.preventDefault()
        this.setState({case_initial_budget:e.target.value,message:''})
    }

    onChangeZipCode(e){
        e.preventDefault()
        this.setState({case_zipcode:e.target.value,message:''})
    }
   
    
    onChange(e){
            e.preventDefault()
            const {name, value} = e.target
            this.setState({[name] : value})
            console.log("titulo caso: " + this.state.case_city)
        }
    
    
   
    getEstado(e) {
        e.preventDefault()
        this.setState({case_state:e.target.value})
        console.log("estado no sincrono: " + this.state.case_state)
    }

   
  

    render(){
        return(
            <div>
            <Nav></Nav>
            <div className="container">
                <h3>Publicar Caso</h3>
              <div>
                <b>Titular:</b> {this.getUsername()}
              </div>
              <br></br>
              <form>
                    <div className="row">
                            <div className="form-group col-md-6">
                                <input type="text" className="form-control col-md-5" placeholder="Titulo del Caso" id="titulo-caso" value={this.state.case_title} onChange={this.onChangeTitle} />
                                <small id="case_description_help" className="form-text text-muted">
                                    Recomendamos poner el nombre de la mascota
                                </small>
                            </div>
                            <div className="form-group col-md-6">
                                <input type="text" className="form-control col-md-7" placeholder="$ - Presupuesto Inicial" value={this.state.case_initial_budget} onChange={this.onChangeBudget}/>
                                <small id="case_description_help" className="form-text text-muted">
                                    Presupuesto Inicial Aproximado que necesita el caso
                                </small>
                            </div>
                            <div className="form-group col-md-6"></div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-6">
                                <textarea className="form-control" placeholder="Descripcion del Caso" value= {this.state.case_description} onChange={this.onChangeDesc}/>
                                <small id="case_description_help" className="form-text text-muted">
                                    Descripcion debe ser de maximo 500 caracteres
                                </small>
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-6">
                                <input type="text" className="form-control col-md-8" placeholder="Ciudad" value= {this.state.case_city} onChange={this.onChangeCity} />
                            </div>
                            
                            <div className="form-group col-md-6">
                                <input type="text" className="form-control col-md-6" placeholder="Codigo Postal" value={this.state.case_zipcode} onChange={this.onChangeZipCode} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="form-group col-md-6">
                                <select id="inputState" className="form-control" onChange={this.getEstado}>
                                    <option selected>Estado...</option>
                                    <option value="Ciudad de Mexico">Cd Mx</option>
                                    <option value="Jalisco">Jalisco</option>
                                    <option value="Queretaro">Queretaro</option>
                                    <option value="Otro">Otro</option>
                                </select>
                            </div>
                            <div className="form-group col-md-6">
                                <button type="button" className="btn btn-outline-primary" onClick={this.onPublishCase}>Publicar</button>
                                <small id="case_description_help" className="form-text text-muted">
                                    {this.state.message}
                                </small>
                            </div>
                        </div>
            </form>
            </div>
            
            <Footer></Footer>
        </div>
        )
    }
        
    
}
export default withRouter(PublishCase)