import React from 'react'
import {HashRouter, Route, Switch} from 'react-router-dom'
import Home from './pages/Home'
import Login from './pages/Login'
import Register from './pages/Register'
import CaseDonation from './pages/CaseDonation'
import './App.css'
import PublishCase from './pages/PublishCase';


class App extends React.Component {
 
  render () {

    return(
      <HashRouter basename="/">
      <div>
      
      <Switch>
      <Route exact path="/" component={Home} />
      <Route exact path="/login" component={Login} />
      <Route exact path="/publishCase" component={PublishCase} />
      <Route exact path="/makeDonation" render={(props) => <CaseDonation {...props} componentDidMount={true} />} />
      
      
      
      <Route exact path="/Register" component={Register} />
      <Route exact path="*" component={() => "404 NOT FOUND"} />
      </Switch>
      
      </div>
      </HashRouter>
    )
  }
}

export default App
